@import "../../vars.scss";

.weather {
    border: 3px solid;
    margin: auto;
    padding: 10px;
    margin-bottom: 20px;
    margin-top: 20px;
    &__td {
        text-align: center;
        border: 1px solid;
        padding: 5px;
        font-style: italic;
    }
}
.form {
    padding: 30px 0;
    text-align: center;
    &__button {
        padding: 10px 30px;
        background: none;
        border: 3px solid rgb(112, 153, 199);
        color: rgb(0, 0, 0);
        font-size: 24px;
        cursor: pointer;
        margin: 5px;
        &:hover:active {
            color: #453688b7;
            border-color: #7f59bdb7;
        }
    }
}
.form input {
    background: none;
    font-size: 30px;
}
.clue {
    display: inline-block;
    font-family: "Montserrat", sans-serif;
    font-weight: 300;
    margin-top: 1%;
    font-size: 19px;
}